const pipeline = (x) => x();

const importers = [
  async (path) => {
    if (!/^\.[\\/](?:packages\/ui\/src\/lib(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(22);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/packages\/ui\/src\/lib(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/ */
      './packages/ui/src/lib/' + pathRemainder
    );
  }
  
];

export async function importFn(path) {
  for (let i = 0; i < importers.length; i++) {
    const moduleExports = await pipeline(() => importers[i](path));
    if (moduleExports) {
      return moduleExports;
    }
  }
}